body{
	overflow-x: hidden;
}

.mt-banner {
	overflow-x: hidden;
	width: 100%;
	color: black;
	text-align: right;
	padding: 22px 0 22px 0;
	border-bottom: solid 3px #ffffff;
	display: flex;
	justify-content: flex-end;
	flex-direction: row;
	align-items: center;
	background-image: linear-gradient(45deg, #131212, #36363a);
}

.mt-title {
	text-shadow: 1px 1px 10px #f0eff5 ;
	padding-left: 62px;
	background-image: linear-gradient(45deg, #908e8e, rgba(177, 172, 172, 0.877), #f0f0f6);
	background-clip: text;
	color: transparent;
	font-size: 3em;
	margin: 0 150px 0 0;
}
.slogan{
	color: #aaa9af;
	text-shadow: 1px 1px 10px #f0eff5 ;
	margin: 0 250px 0 0;
	font-size: 1em;
}

.mt-logo {
	height: 95px;
	width: 95px;
}
.right-star{
	position: absolute;
	padding: 0 200px 0 50px;
}
.left-star{
	margin: 0 25px 0 0;
} 
