@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300&display=swap');

.footer-mt-logo{
	position: absolute;
	margin: 0 0 0 -450px;
	height: 95px;
	width: 95px;
}
.nameFooter{
	position: absolute;
	margin: 100px 0 0 -970px;
	text-shadow: 1px 1px 10px #f0eff5 ;
	padding-left: 62px;
	background-image: linear-gradient(45deg, #908e8e, rgba(177, 172, 172, 0.877), #f0f0f6);
	background-clip: text;
	color: transparent;
	font-size: 1.5em;
}
.mt-footer {
	color: #807f83;
	padding: 22px 0 22px 0;
	border-top: solid 3px #ffffff;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	align-items: center;
	font-weight: 500;
	background-image: linear-gradient(45deg, #131212, #36363a);
	height: 350px;
	width: 100%;
	max-width: 100%;
}
.mt-footer-elem {
	font-size: 1.2em;
	margin-bottom: 10px;
	font-family: 'Chakra Petch' ;
	text-shadow: 1px 1px 10px #f0eff5 ;
}
input{
	font-size: 1.2em;
	font-family: 'Chakra Petch' ;
	background-color: rgb(25, 25, 27) !important;
	color: #aaa9af !important;
	width: 20%;
	height: 25px;
	border-radius: 5px;
	margin: 0 0 35px 0;
}
input:hover{
	background-color: #aaa9af !important;
	color: rgb(25, 25, 27) !important;
}
